

h3{
    color:white;
    margin-top: auto;
    margin-bottom:auto;
}

.title{
    display: flex;
    flex-direction: row;
    justify-content: space-between;
    background-color: #FF6060;
    padding:10px 20px 10px 15px;
    border-radius: 5px;
    font-size: 18px;
    font-weight: 500;
    color:white;
    }

.title:hover{
    cursor:pointer;
}

.collapse{
    margin-bottom: 20px;
}

.arrow {
    transition: transform 0.8s;
  }
  
.arrow.rotate {
    transform: rotate(180deg);
  }

.content {
    max-height: 0;
    display: none;
    transition: max-height 0.1s ease;
    font-size: 18px;
    background-color: #F6F6F6;
    color:#FF6060;
    padding:20px;
  }
  
.open {
    display:flex;
    max-height: 500px; 
  }
